	import topBar from '../../components/TopBar.vue'
	import ListItem from '../../components/ListItem.vue'
	import ButtonItems from '../../components/ButtonItems.vue'
	import selectItems from '../../components/selectItems.vue'
	
    export default {
		data(){
			return{
				userInfo:{},
				statePosition: '',
				endPosition: '',
				currentPosition: '',
				position:{width:'0px'},
				touch: false,
				amount:'2000',
				subState: false,
				period:'12',
				purpose: '个人生活消费',
				themeColor: '#007AFF',
				mode: '',
				deepLength: 1,
				pickerValueDefault: [0],
				pickerValueArray:[],
				pickerSingleArray: [{
						label: '3',
						value: 1
					},
					{
						label: '6',
						value: 2
					},
					{
						label: '12',
						value: 3
					},
					{
						label: '24',
						value: 4
					}
				],
			}
		},
		components: {
			topBar,
			ListItem,
			ButtonItems,
			selectItems
		},
		onLoad() {
			this.getUserInfo()
		},
		onReady() {
			this.getElSize().then(res => {
				this.endPosition = res.width + this.statePosition
			})
		},
        methods: {
            goPath(url){
				uni.navigateTo({ url });
			},
			submitHandle(){
				const data = {amount:this.amount, period:this.period*30, purpose:this.purpose}
				if (this.userInfo.isBindCard) {
					this.request({
						url: `transaction/my/projects`,
						method: 'POST',
						params: data
					}).then(res => {
						this.goPath('/pages/borrow/audit?title="申请成功"')
					}).catch(err => {
						uni.showToast({
							icon: 'none',
							title: '申请失败，请稍后重试！'
						});
					})
					
				}else{
					this.goPath('/pages/lend/openStorage')
				}
			},
			showSinglePicker() {
				this.pickerValueArray = this.pickerSingleArray
				this.mode = 'selector'
				this.deepLength = 1
				this.pickerValueDefault = [0]
				this.$refs.mpvuePicker.show()
			},
			// 确定
			onConfirm(e) {
				this.period = e.label
			},
			blurHandle(){
				if (this.amount < 2000 ){
					this.amount = 2000
				}
			},
			setAmount(event){
				const st = event.target.value
				this.amount = st
				if ( Number(st) > Number(this.userInfo.loanAmount)){
					uni.showToast({
                        icon: 'none',
                        title: '申请金额超出上限，请重新输入'
                    });
					this.amount = this.userInfo.loanAmount
				}
				
				this.amountToPosition({st})
			},
			getUserInfo(){
				this.request({
					url: `consumer/my/consumers`,
				}).then(res => {
					this.userInfo = res.data.result
					uni.setStorage({key:'userInfo', data: res.data.result})
				}).catch(err => {
					uni.showToast({
                        icon: 'none',
                        title: '用户信息获取失败，重新登录！'
                    });
				})
			},
			touchStateHandle(event){
				this.statePosition = event.currentTarget.offsetLeft
				this.touch = true
				this.getElSize().then(res => {
					this.endPosition = res.width + this.statePosition
					this.setMovePosition(event.changedTouches[0].pageX)
				})
			},
			touchmoveHandle(event){
				this.setMovePosition(event.changedTouches[0].pageX)
			},
			touchendHandle(){
				this.touch = false
			},
			setMovePosition(st, obj){
				let wd = 0;
				let ps = st
				
				if (ps<= this.statePosition){
					wd = 0
				}else if (ps >= this.endPosition){
					wd = this.endPosition - this.statePosition
				}else{
					wd = st - this.statePosition
				}
				this.currentPosition = wd
				this.position = `width: ${wd}px`
				if (!obj) {
					this.amountToPosition({wd})
				}
			},
			amountToPosition({wd,st}){
				if (wd){
					let perc = wd / (this.endPosition - this.statePosition)
					let num = ((this.userInfo.loanAmount - 2000) * perc + 2000)/100
					this.amount = num.toFixed(0) * 100
				}else if (st){
					let perc = st / (this.userInfo.loanAmount - 2000)
					let num = (this.endPosition - this.statePosition) * perc
					this.setMovePosition(num, true)
				}
				
			},
			getElSize() { //得到元素的size
				return new Promise((res, rej) => {
					uni.createSelectorQuery().select("#progBox").fields({
						size: true,
						scrollOffset: true
					}, (data) => {
						res(data);
					}).exec();
				})
			},
			redioHandle(){
				this.subState = true
			}
        }
    }
